.catalog {
  padding: 0 40px;
}

.row {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 403px;
  padding: 30px 14px 13px;
  font-family: "Roboto", sans-serif;

  &:not(:last-child) {
    margin-right: 48px;
  }

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 5px;
    margin-bottom: 41px;
  }

  &__discription {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }

  &__code {
    font-weight: normal;
    font-size: 11px;
    line-height: 13px;
    margin-top: 5px;
  }

  &__rate {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

}

.rate-stars {
  display: flex;
  height: 15px;
}

.star {
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 15px;
  margin-right: 4px;
}

.rate-stars--1 .star:nth-child(-n+1),
.rate-stars--2 .star:nth-child(-n+2),
.rate-stars--3 .star:nth-child(-n+3),
.rate-stars--4 .star:nth-child(-n+4),
.rate-stars--5 .star:nth-child(-n+5) {
  background-image: url("../images/star-active.svg");
}

.rate-count {
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  text-align: right;
}

.price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 20px;

  &__text {
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    text-align: right;
  }

  &__number {
    font-weight: bold;
    font-style: normal;
    font-size: 16px;
    line-height: 19px;
    text-align: right;
  }
}

.buy {
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: white;
  text-transform: uppercase;
  height: 40px;
  width: 170px;
  background: #00acdc;
  border-radius: 5px;
  margin-top: 20px;
  padding: 12px 57px 11px 58px;
  transition: 250ms;

  &:hover {
    color: #00acdc;
    background: white;
    border: 1px solid #00acdc;
  }
}
